import {GetDataList} from '@/api/class-center'
export default {
	data(){
		return{
			grouplist:[],//小组列表
			getParameters:{
				a:'getplaytime',
				playtime:'group',//小组‘group’，问答‘ask’，作品‘works’，资讯‘information’
				pageindex:1,
				pagesize:10
			},
			pageCount:0,
			refreshStatus:false,
			height:0
		}
	},
	mounted(){
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight-44
			}
		})
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
		},
		openUrl(url){
			uni.navigateTo({
			    url:url
			});
		},
		scrolltolower(){
			this.getParameters.pageindex++
			if(this.getParameters.pageindex <= this.pageCount){
				this.loadPage('more')
			}
		},
		//初始数据的请求
		loadPage(status){
			if(this.loading){
				return
			}
			this.loading = true
			if(status=='more'){
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.grouplist = this.grouplist.concat(res.groupmodel.groupList)
					}
					this.loading = false
				})
			}else{
				this.getParameters.pageindex=1
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.groupmodel.pageCount
						this.grouplist = res.groupmodel.groupList
					}else{
						this.grouplist = []
					}
					uni.hideLoading();
					this.loading = false
					setTimeout(()=>{
						this.refreshStatus = false
					},1000)
				})
			}
			
		}
		
	}
}
